@import url(https://fonts.googleapis.com/css?family=Russo+One);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{background:#8e87b5;margin:10px 0;max-width:70%;margin:auto;text-align:center}#preview,.title{display:inline-block}#preview{border:3px solid #224b4b;padding:10px}#preview blockquote{border-left:3px solid #224b4b;color:#224b4b;padding-left:5px;margin-left:25px}#preview code{background:white;padding:1px 4px 2px 4px;font-size:12px;font-weight:bold}#preview pre{background:white;padding:5px 0 5px 5px}#preview h1{border-bottom:2px solid #30224b}#preview h2{border-bottom:1px solid #30224b}#preview table{border-collapse:collapse}#preview td,#preview th{border:2px solid #30224b;padding-left:5px;padding-right:5px}

